import { MAX_FILE_NAME_LENGTH, NonEmptyString } from './BaseString';

export class FileName extends NonEmptyString<'FileName', string> {
  static from(value: string): FileName {
    return new FileName(value);
  }
  protected validate(value: string): string {
    value = super.validate(value);

    if (value.length > MAX_FILE_NAME_LENGTH) {
      throw new Error(`FileName has maximum length of ${MAX_FILE_NAME_LENGTH}`);
    }

    return value;
  }
}
